.colaborador{
    width: 250px;
    margin-bottom: 24px;
    position: relative;


}

.colaborador .encabezado{
    background-color: #F0F0F0;
    border-radius: 10px 10px 0px 0px;
}

.colaborador img{
    width: 100px;
    border-radius: 50%;
    position: relative;
    bottom: -50px;
}

.colaborador .info{
    background-color: #FFFFFF;
    box-shadow: 5px 5px 15px rgba(0, 0,0, 0.08);
    border-radius: 0px 0px 10px 10px;
    padding-top: 90px;
    padding-bottom: 40px;
    min-height: 100px;
    font-family: 'Montserrat', sans-serif;
}

.colaborador .info h4{
    font-weight: bold;
    color: #6278f7;
    font-size: 18px;
    margin-bottom: 8px;
    line-height: 22px;
}

.colaborador .info h5{
    font-weight: 400;
    color: #212121;
    font-size: 18px;
    line-height: 22px;
    padding: 0 24px;
    font-weight: 400;
    margin: 0;
}

.eliminar{
    position: absolute;
    top: 0px;
    right: 0px;
    cursor: pointer;
    color: #FFFFFF;
}